<!--患者留言页面-->
<template>
  <div id="tmpl">
    <div class="searchGroup">
      <el-date-picker
        class="timeChoice"
        v-model="stimeValue"
        type="date"
        size="small"
        placeholder="选择日期">
      </el-date-picker>
      <el-date-picker
        class="timeChoice"
        v-model="etimeValue"
        type="date"
        size="small"
        placeholder="选择日期">
      </el-date-picker>
      <el-button type="primary" size="small" @click="searchClick">查询</el-button>
    </div>

    <div>
      <el-table
        :data="tableDatas"
        height="250"
        border
        style="width: 100%">
       <!-- <el-table-column
          label="头像">
          <img :src="tableDatas.headimgurl" alt=""/>
        </el-table-column>-->
        <el-table-column
          prop="userName"
          label="姓名">
        </el-table-column>
        <el-table-column
          prop="messageDate"
          label="日期">
        </el-table-column>
        <el-table-column
          prop="message"
          label="留言内容">
        </el-table-column>
      </el-table>
    </div>

    <div class="fenye">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="currentPage"
        :page-sizes="[5, 10, 15, 20]"
        :page-size="size"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </div>
  </div>
</template>

<script>
  export default {
    data:function(){
      return {
        stimeValue:'',//开始日期值
        etimeValue:'',//结束日期值
        tableDatas:[],//表格数据
        //分页使用数据
        currentPage: 1,
        total:5,
        size:5
      }
    },
    created:function(){
      this.getTableData();
    },
    mounted:function(){},
    methods:{
      //点击按钮查询表格
      searchClick:function(){
        this.getTableData();
      },
      //获得表格数据
      getTableData:function(){
        var url = '/api/com/fee/getFeeBackList';
        this.$http.post(url,{
          sDate:this.stimeValue,
          eDate:this.etimeValue,
          page:this.currentPage,
          size:this.size
        },{emulateJSON:true}).then(function(res){
            console.log(res);
          if(res.body.zt == 'true'){
            this.tableDatas = res.body.data;
            this.total = res.body.total;
          }
        });
      },
      //分页使用函数
      handleSizeChange:function(val) {
        console.log(`每页 ${val} 条`);
        this.size = val;
        this.getTableData();
      },
      handleCurrentChange:function(val) {
        console.log(`当前页: ${val}`);
        this.currentPage = val;
        this.getTableData();
      }
    }
  }
</script>

<style scoped>
  .timeChoice{
    width:150px;
  }
  .fenye{
    text-align: center;
  }
  .searchGroup{
    box-sizing: border-box;
    padding:15px 0;
    height:62px;
  }
  #tmpl{
    margin-top: 60px;
    text-align: left;
  }
</style>



