<template>
  <div id="tmpl" class="hello">
    <div id="searchGroup">
      <el-input size="small" v-model="opeName" class="searchInput" placeholder="请输入要查询手术编码/名称" clearable></el-input>
      <!--<p>{{this.opeName}}</p>-->
      <el-button type="primary" @click="getSearchContent" size="small">查询</el-button>
      <el-button type="primary" size="small">保存</el-button>
      <el-popover
          v-model="visible2"
          id="alertDiv"
          tooltip-effect="dark"
          ref="multipleTable"
          placement="bottom"
          width="1200"
          trigger="click">
        <el-input size="small" v-model="opeName2" class="searchInput" style="margin-top:0px;margin-right:8px" placeholder="请输入要查询手术编码/名称" clearable></el-input>
        <el-button type="primary"  @click="getSearchContent2" size="small">查询</el-button>
        <el-button type="primary" @click="closeClickRow" size="small">添加</el-button>
        <el-button type="primary" slot="reference" size="small">增加</el-button>


        <el-table
            @selection-change="changeFun"
            :select-on-indeterminate="selectTrueOrFalse"
            :data="tableData2"
            ref="moviesTable"
            tooltip-effect="dark"
            style="width: 100%">
          <el-table-column :selectable='checkboxInit' type="selection"></el-table-column>
          <el-table-column prop="operationCode" label="ICD9-CM3代码" show-overflow-tooltip></el-table-column>
          <el-table-column prop="opeationName" label="ICD9-CM3名称" show-overflow-tooltip></el-table-column>
          <el-table-column prop="operationLevel" label="ICD9-CM3等级" show-overflow-tooltip></el-table-column>
          <el-table-column prop="stdIndicator" label="标准标志" show-overflow-tooltip></el-table-column>
          <el-table-column prop="approved" label="启用标志" show-overflow-tooltip></el-table-column>
          <el-table-column prop="createDate" label="创建日期" show-overflow-tooltip></el-table-column>
          <el-table-column prop="inputCode" label="输入码" show-overflow-tooltip></el-table-column>
          <el-table-column prop="creator" label="创建人" show-overflow-tooltip></el-table-column>
        </el-table>
        <div class="block">
          <el-pagination
            class="pull-right clearfix"
            @size-change="handleSizeChange2"
            @current-change="handleCurrentChange2"
            :current-page="pageNo2"
            :page-sizes="pageSizesList2"
            :page-size="5"
            layout="total, sizes, prev, pager, next, jumper"
            :total="totalDataNumbers2">
          </el-pagination>
        </div>
      </el-popover>
      <el-button type="primary" @click="centerDialogVisible = true" size="small">删除</el-button>
      <el-dialog
        title="提示"
        :visible.sync="centerDialogVisible"
        width="30%"
        center>
        <span>确定删除选中项吗？</span>
        <span slot="footer" class="dialog-footer">
          <el-button @click="centerDialogVisible = false" size="small">取 消</el-button>
          <el-button type="primary" @click="removeData" size="small">确 定</el-button>
        </span>
      </el-dialog>
    </div>

    <div>
      <el-table
        @selection-change="changeFun2"
        @cell-click="clickName"
        :data="tableData"
        ref="multipleTable"
        tooltip-effect="dark"
        style="width: 100%">
        <el-table-column type="selection"></el-table-column>
        <el-table-column prop="creator" label="创建人" show-overflow-tooltip>
          <template slot-scope="scope">
            <span style="color:#09f;cursor: pointer">{{ scope.row.creator }}</span>
          </template>
        </el-table-column>
        <el-table-column prop="operationCode" label="手术编码" show-overflow-tooltip></el-table-column>
        <el-table-column prop="opeationName" label="手术名称"></el-table-column>
        <el-table-column prop="operationLevel" label="手术等级"></el-table-column>
        <el-table-column prop="createDate" label="创建日期" show-overflow-tooltip></el-table-column>
      </el-table>
      <div class="block">
        <el-pagination
          class="pull-right clearfix"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="pageNo"
          :page-sizes="pageSizesList"
          :page-size="10"
          layout="total, sizes, prev, pager, next, jumper"
          :total="totalDataNumbers">
        </el-pagination>
      </div>
    </div>

  </div>
</template>

<script>
export default {
  data:function(){
    return {
      //设置弹框的显示隐藏
      selectTrueOrFalse:false,
      visible2:false,
      opeName:'',
      opeName2:'',
      tableData: [],//表格结果集
      tableData2: [],//表格结果集
      totalDataNumbers: 0,//分页数据的总数,
      totalDataNumbers2: 0,//分页数据的总数,
      currentPage1: 1,//当前分页显示第1页
      currentPage2: 1,//当前分页显示第1页
      //分页所用数据
      pageNo: 1,//页数
      pageNo2: 1,//页数
      pageSize: 10,//每页的条数
      pageSize2: 5,//每页的条数
      pageSizesList: [10, 15, 20, 30, 50],//选择每页的条数
      pageSizesList2: [3,5,10, 15],//选择每页的条数

      //初始化点击的行的ID值
      ids:'',
      ids2:'',
      moviesTable1:[],
      //隐藏删除确认框
      centerDialogVisible:false
    }
  },
  created:function(){
    this.getPageNation();//执行获取数据的函数
    this.getOutPageNation();//执行获取数据的函数

  },
  methods:{
    /*-------------- 四、格式化名称列的数据为可点击 -------*/
    clickName:function(row, column, cell, event){
      if(column.label == '创建人'){
        //console.log(row.id);
        var operationId = row.id;
        var url = '/api/com/operation/getOperationEducation';
        this.$http.get(url,{params:{opeId:operationId}}).then(function(res){
          if(res.body){
            this.$router.push({name:'doctorChoice',params:{data:res.body}});
          }
        });
      }
    },
    /*handleEdit:function(index, row) {
      this.$router.push('/doctorChoice');  //将你的跳转写在这里。
    },*/
    /* ---------------- 三：删除数据 --------------- */
    //获得选中行的id
    changeFun2:function(val){
      if(val.length>0){
        for(var i=0;i<val.length;i++){
          var vals = val[i];
        }
        this.ids2 += vals.id+',';
      }else{
        return
      }
      //console.log(this.ids);
    },
    //点击弹出提示框的确定按钮，执行删除操作
    removeData:function(){
      var ss = this.ids2.split(',');
      // console.log(ss);
      //方法一:ES6数组去重
      //var s6 = [...new Set(ss)];
      var res = [];
      var json = {};
      for(var i = 0; i < ss.length; i++){
        if(!json[ss[i]]){
          res.push(ss[i]);
          json[ss[i]] = 1;
        }
      }
      var removeIds = res.join(',');
      //console.log(removeIds);
      var url = '/api/com/operation/deleteOperationById';
      this.$http.get(url,{params:{ids:removeIds}}).then(function(res){
        if(res.body.zt === 'true'){
          //alert('删除成功');
          this.getPageNation();
        }else{
          alert('删除失败');
          return;
        }
      });
      this.centerDialogVisible = false;
    },
   /* -------------二：添加记录 ------------*/
    //如果当前行的bz为0时，设置当前能选中
    checkboxInit:function(row){
      //console.log(row);
      if(row.bz != 0){
        return 0;
      }else{
        return 1;
      }
    },
    //2、复选框勾选时获取当前项的值
    changeFun:function(val){
      if(val.length>0){
        for(var i=0;i<val.length;i++){
          var vals = val[i];
        }
        this.ids += vals.id+',';
      }else{
        return
      }
      //console.log(this.ids);
    },
    //1、点击当前行的时复选框被选中，否则取消选中
    /*clickRow:function(row){
      this.$refs.moviesTable.toggleRowSelection(row);
      if(row.bz != 0){
        return 0;
      }else{
        return 1;
      }
    },*/
    //点击弹出框表格中的行，添加到外层表格中
    //3、点击弹框中确定按钮执行的操作(关闭表格弹框、获取行数据的ID并发送给外层表格)
    closeClickRow:function(row,event){
      //console.log(this.ids);
      //获得的id值参数去重
      var ss = this.ids.split(',');
     // console.log(ss);
      //方法一:ES6数组去重
      //var s6 = [...new Set(ss)];
      var res = [];
      var json = {};
      for(var i = 0; i < ss.length; i++){
        if(!json[ss[i]]){
          res.push(ss[i]);
          json[ss[i]] = 1;
        }
      }
      var idsGruop = res.join(',');
      //console.log(idsGruop);
      //s6 = s6.join(',');
      //idsGroup = s6;
      //console.log(idsGroup);
      var url = '/api/com/operation/inserOperation';
      this.$http.get(url,{params:{ids:idsGruop}}).then(function(res){
        //console.log(res.body.zt);
        //alert(res.zt);
        if(res.body.zt === 'true'){
          this.getPageNation();
          this.ids = '';
          this.visible2 = false;
        }else{
          alert('已经添加过了,不能重复添加');
        }
      });
      //console.log(this.moviesTable);
      //alert('fda');
      this.opeName2 = '';
      this.selectTrueOrFalse = false;
    },


    //点击查询时，获取input内容
    getSearchContent:function(){
      this.getPageNation();
    },
    getSearchContent2:function(){
      this.getOutPageNation();
    },

    handleCurrentChange:function(val) {
      this.currentPage1 = val;
      //console.log(val);
      this.getPageNation();
    },
    handleCurrentChange2:function(val2) {
      this.currentPage2 = val2;
      //console.log(val);
      this.getOutPageNation();
    },
    handleSizeChange:function(size) {
      this.pageSize = size;
      //alert('每页显示的条数发生改变');
      //console.log(size+'ss');
      this.getPageNation();
    },
    handleSizeChange2:function(size2) {
      this.pageSize2 = size2;
      //alert('每页显示的条数发生改变');
      //console.log(size+'ss');
      this.getOutPageNation();
    },
    /* --------------一：获取数据 ----------------- */
    //分页
    //分页请求数据
    getPageNation:function(){
      //console.log(this.currentPage1+': 当前页数; ',this.pageSize+': 每页的条数; ',this.opeName+': 搜索的内容')
      var url = '/api/com/operation/getOperationList';
      this.$http.get(//请求数据的方式
        url,//请求数据的地址
        {params:{name:this.opeName,page:this.currentPage1,size:this.pageSize}}
      ).then(function (res) {//结果处理函数
        //console.log(this.opeName);
        if (res) {//如果请求数据大于零
          //console.log(res.body);
          this.tableData = res.body.tableData;//请求数据的
          this.totalDataNumbers = res.body.totalDataNumber;
          //console.log(this.tableData);
          this.listLoading=false;
        } else {
          this.tableData = [];
          this.totalDataNumbers = 0;
        }
      })
    },
    //弹框表格请求
    getOutPageNation:function() {
      var url = '/api/com/operation/getIcd9ListByName';
      this.$http.get(//请求数据的方式
        url,//请求数据的地址
        {params:{name:this.opeName2,page:this.currentPage2,size:this.pageSize2}}
      ).then(function (res) {//结果处理函数
          //console.log(this.opeName);
          if (res) {//如果请求数据大于零
            //console.log(res.body);
            this.tableData2 = res.body.tableData;//请求数据的
            this.totalDataNumbers2 = res.body.totalDataNumber;
            /*if(res.body.tableData.bz == 0){
              console.log('0000')
            }*/
            //console.log(res.body.tableData.bz);
            this.listLoading=false;
          } else {
            this.tableData2 = [];
            this.totalDataNumbers2 = 0;
          }
        })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  #tmpl{
    margin-top: 60px;
  }
.searchInfo{
  width:100%;
  height:62px;
  background-color: red;
}
.searchInput{
  float:left;
  margin-top:8px;
}
#searchGroup{
  height:62px;
}
#alertDiv>button,
#searchGroup>button{
  margin-top:8px;
  margin-left:8px;
  float:left;
}
  .searchInput{
    width:220px;
    height:50px;
    margin-left:0px;
    text-align: left;
  }
#opeTable{
  border-collapse:collapse;
  width:90%;
  margin-left:5%;
}
#opeTable tr td{
  border:1px solid #ccc;
  height:30px;
}
#opeTable tr td:nth-child(1){
  width:30px;
}
.el-main .el-table{
  text-align: left;
}
</style>



