<template>
  <div>

    <el-container>
      <el-container>
        <el-aside :style="asides">
          <ul id="sideListStyle">
            <li v-for="(item,index) in leftMenuList" @click="leftMenuClick(index)" :class="resultNum==index?'leftMenuActive':''">
              <router-link :to="item.url" :class="resultNum==index?'leftMenuActive':''">{{item.name}}</router-link>
            </li>
          </ul>
        </el-aside>
        <el-header height="80px">
          日间手术信息系统
          <div style="float:right;font-size:16px">
            <span>
              欢迎您，{{ uesrName }}
            </span>
            <span @click="outSys" style="cursor:pointer;margin-left:10px">退出</span>
          </div>
        </el-header>

        <el-main id="ss" :style="widths">
          <router-view></router-view>
        </el-main>
        <el-footer height="30px">京弘达信息技术（北京）有限公司</el-footer>
      </el-container>
    </el-container>
  </div>
</template>
<script>
  export default {
    data:function(){
      return {
        uesrName:'',
        widths:{
          marginLeft:'200px'
        },
        asides:{
          width:'200px',
          position:'fixed',
          top:'80px',
          backgroundColor:'#f2f2f2'
        },
        //侧边栏的数据（名字、URL）
        leftMenuList:[],
        //设置数据，为侧边栏点击提供比较
        num:0
      }
    },
    created:function(){
      //在导航部分显示用户名
      this.uesrName = sessionStorage.getItem('USERNAME').split('-')[1];
      //使用初始化宽高的函数
      this.setWH();
      //获得菜单列表
      this.leftMenuList = JSON.parse(sessionStorage.getItem('menu'));
    },
    mounted:function(){
    },
    computed:{
      resultNum:function(){
        this.num = sessionStorage.getItem('MENUMUNBER');
        //console.log(this.num);
        return this.num;
      }
    },
    methods:{
      //退出系统函数
      outSys:function(){
        sessionStorage.clear('token');
        sessionStorage.clear('USERNAME');
        window.location.reload();
      },
      //侧边栏高亮显示点击事件
      leftMenuClick:function(index){
        //this.num = index;
        //console.log(this.num);
        sessionStorage.setItem('MENUMUNBER',index);
        this.num = sessionStorage.getItem('MENUMUNBER');
      },
      //初始化宽高
      setWH:function(){
        var windowH = window.innerHeight-110;
        this.asides.height = windowH+'px';
        this.widths.minHeight = windowH-10+'px';
        //console.log(windowH);
        /*var windowW = window.innerWidth-220;
         this.widths.maxWidth = windowW+'px';*/
      }
    }
  }
</script>

<style>

  *{
    margin:0;
    padding:0;
    list-style: none;
    text-decoration: none;
  }
  #app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
  }
  .el-header, .el-footer {
    background-color: #0059B3;
    color: #fff;
    position:fixed;
    width:100%;
    z-index:1000;
  }
  .el-header{
    top:0px;
    line-height: 80px;
    font-size:28px;
    text-align:left;
  }
  .el-footer{
    bottom:0px;
    line-height: 30px;
    height: 30px;
    text-align: center;
    font-size:14px;
  }
  .el-aside {
    background-color: #D3DCE6;
    color: #333;
    text-align: center;
  }

  .el-main {
    background-color: #FCFCFC;
    color: #333;
    text-align: center;
    margin-top:20px;
    margin-bottom:30px;
    box-sizing: border-box;
    padding:5px;
  }

  body > .el-container {
    margin-bottom: 40px;
  }

  .el-container:nth-child(5) .el-aside,
  .el-container:nth-child(6) .el-aside {
    line-height: 260px;
  }

  .el-container:nth-child(7) .el-aside {
    line-height: 320px;
  }
  #sideListStyle{
    width:100%;
    height:100%;
  }
  #sideListStyle li {
    width:100%;
    height:50px;
    line-height:50px;
  }
  #sideListStyle li a:hover{
    color:#0059b3;
  }
  #sideListStyle li a {
    display: inline-block;
    width:100%;
    height:100%;
    line-height:50px;
    text-align: left;
    box-sizing: border-box;
    padding:4px 0px 4px 10px;
    color:#333;
    text-decoration: none;
  }
  #sideListStyle li .leftMenuActive{
    color:#0059b3;
  }
  .loginGroup{
    box-sizing: border-box;
    padding:10px 40px;
    width:300px;
    height:200px;
    position:fixed;
    top:150px;
    left:40%;
    border:1px solid #0074D9;
    border-radius: 5px;
  }
  .loginGroup input{
    outline:none;
    border:1px solid #2b81af;
    margin-bottom:10px;
  }
</style>



