import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)

export default new Vuex.Store({
  state:{
    count:0,
    menu:[]
  },
  mutations: {
    MENU (state,n) {
      state.menu = sessionStorage.getItem('menu');
      //console.log(state.menu);
    }
  },
  getters: {
    getmenus: state => {
      return state.menu;
    }
  }

})



