<!--角色管理-->
<template>
  <div id="tmpl">
    <div id="searchGroup">
      <el-input v-model="roleName" class="inputs" placeholder="请输入角色名称" clearable size="small"></el-input>
      <el-button type="primary" size="small" @click="searchRole">查询</el-button>
      <el-button type="primary" size="small" @click="dialogShowOrHide">添加</el-button>
      <div style="float:right">
        <el-input
          class="treeInput"
          size="small"
          placeholder=""
          v-model="filterText">
        </el-input>
        <el-button type="primary" size="small" @click="getCheckedNodes">确定</el-button>
        <el-button type="primary" size="small" @click="choiceAll">全选</el-button>
      </div>
      <el-dialog title="增加" :visible.sync="dialogFormVisible">
        <el-form>
          <el-form-item label="角色名称" :label-width="formLabelWidth">
            <el-input v-model="boxRoleName" auto-complete="off"></el-input>
          </el-form-item>
          <!--<el-form-item label="角色类型" :label-width="formLabelWidth">
            <el-radio v-model="radioChoice" label="1">管理员</el-radio>
            <el-radio v-model="radioChoice" label="2">普通用户</el-radio>
          </el-form-item>-->
          <el-form-item label="备注" :label-width="formLabelWidth">
            <el-input type="textarea" :autosize="{ minRows: 2, maxRows: 4}"
              placeholder="请输入内容"
              v-model="textareas">
            </el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogFormVisible = false">取 消</el-button>
          <el-button type="primary" @click="addNewRole">确 定</el-button>
        </div>
      </el-dialog>
      <el-button type="primary" size="small" @click="delRoleRow">删除</el-button>
    </div>
    <el-row :gutter="10">
      <el-col :span="16">
        <div class="grid-content bg-purple">
          <el-table
            ref="multipleTable"
            :data="tableData"
            tooltip-effect="dark"
            style="width: 100%"
            @cell-click="clickRow"
            @selection-change="handleSelectionChange">
            <el-table-column
              type="selection"
              width="55">
            </el-table-column>
            <el-table-column
              prop="name"
              label="角色名称">
              <template slot-scope="scope">
                <span style="color:#09f;cursor: pointer">{{ scope.row.name }}</span>
              </template>
            </el-table-column>
            <!--<el-table-column
              prop=""
              label="状态">-->
            </el-table-column>
            <el-table-column
              prop="descript"
              label="备注"
              show-overflow-tooltip>
            </el-table-column>
          </el-table>
          <div style="text-align: center">
            <el-pagination
              @size-change="handleSizeChange"
              @current-change="handleCurrentChange"
              :current-page="page"
              :page-sizes="[5, 10, 15, 20]"
              :page-size="size"
              layout="total, sizes, prev, pager, next, jumper"
              :total="total">
            </el-pagination>
          </div>
        </div>
      </el-col>
      <el-col :span="8">
        <div class="grid-content bg-purple">

          <el-tree
            v-loading="loading"
            :data="treeData"
            show-checkbox
            default-expand-all
            node-key="id"
            ref="tree"
            :props="defaultProps">
          </el-tree>
        </div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
  export default {
    data:function(){
      return {
        //loading显示隐藏
        loading:false,
        //删除角色时保存的ID
        delRoleID:'',
        //获得其中一条数据的ID
        roleId:'',
        //弹出框中数据初始化
        boxRoleName:'',//输入框input值
        textareas:'',//文本域
        //radioChoice:'1',//单选框
        //弹出框数据
        dialogFormVisible: false,
        formLabelWidth: '120px',
        //输入框的值
        roleName:'',
        //表格数据
        tableData:[],
        //分页数据
        page:1,
        size:5,
        total:0,
        //树形控件需要的值
        checkedList:[],//默认选中的值
        filterText: '',
        filterId:0,
        treeData:[],
        defaultProps: {
          children: 'children',
          label: 'label'
        },
        //标志树形控件按钮的点击次数
        treeClickNum:false
      }
    },
    created:function(){
      //展示数据
      this.getData();
      //展示树的数据
      this.getTreeData();
    },
    methods:{
      //树形控件全选
      choiceAll:function(){
        this.treeClickNum = !this.treeClickNum;
        if(!this.treeClickNum){
          this.$refs.tree.setCheckedNodes([]);
        }else{
          this.$refs.tree.setCheckedNodes(this.treeData);
        }
      },
      //新增角色与菜单之间的关系
      getCheckedNodes:function(){
        //console.log(this.delRoleID);
        var arr = this.delRoleID.split(',');
        //console.log(arr.length,arr,this.delRoleID);
        if(arr.length>2){
          this.$message({
            showClose: true,
            type: 'warning',
            message: '最多只能选择一个角色！'
          });
        }else if(arr.length<2){
          this.$message({
            showClose: true,
            type: 'warning',
            message: '请选择一个角色！'
          });
        }else{
          var rids = this.$refs.tree.getCheckedNodes(true,false);
          var rid = '';
          for(var i=0;i<rids.length;i++){
            rid += rids[i].id+',';
          }
          //console.log(arr[0]+' :表格ID');
          var arrs = arr[0]*1;
          //console.log(arrs);
          //console.log(this.delRoleID+' :表格中的ID');
          //console.log(rid+' :树节点中的ID');
          var url = '/api/com/role/insertRoleAndPermission';
          this.$http.post(url,{
            rid:arrs,
            ids:rid
          },{emulateJSON:true}).then(function(res){
              if(res.body.zt == 'true'){
                this.$message({
                  showClose: true,
                  type: 'success',
                  message: '角色设置成功！'
                });
              }
          });
        }
      },
      //树tree展示数据
      getTreeData:function(){
        this.loading = true;
        var url = '/api/com/role/getPermissionTree';
        this.$http.post(url,{emulateJSON:true}).then(function(res){
          //console.log(res);
          if(res.body.zt == 'true'){
            //console.log(res);
            this.loading = false;
            this.treeData = res.body.data;
          }
        });
      },
      //删除选中行的角色
      delRoleRow:function(){
        if(this.delRoleID == ''){
          this.$message({
            showClose: true,
            type: 'warning',
            message: '请先选择要删除的行!'
          });
        }else{
          var sure = confirm('确定删除选中的行？');
          if(sure == true){
            var url = '/api/com/role/deleteRoleById';
            this.$http.post(url,{ids:this.delRoleID},{emulateJSON:true}).then(function(res){
              this.$message({
                showClose: true,
                type: 'success',
                message: '删除成功!'
              });
              //展示数据
              this.getData();
            });
          }else{
            this.$message({
              showClose: true,
              type: 'warning',
              message: '您已经取消删除!'
            });
          }
        }
      },
      //新增角色、修改角色
      addNewRole:function(){
        this.dialogFormVisible = false;
        if(this.roleId == ''){
          this.roleId = 0;
        }
        var url = '/api/com/role/insertRole';
        this.$http.post(url,{
          id:this.roleId,
          name:this.boxRoleName,
          descript:this.textareas
        },{emulateJSON:true}).then(function(res){
            //console.log(res);
            if(res.body.zt == 'true'){
              if(this.roleId == 0){
                this.$message({
                  showClose: true,
                  message: '成功添加一条数据',
                  type: 'success'
                });
              }else{this.$message({
                showClose: true,
                message: '成功修改一条数据',
                type: 'success'
              });
              }
              //展示数据
              this.getData();
            }
        });
      },
      //出框的显示隐藏
      dialogShowOrHide:function(){
        this.dialogFormVisible = !this.dialogFormVisible;
      },
      //按名称查询表格
      searchRole:function(){
        //console.log(this.roleName);
        //展示数据
        this.getData();
      },
      //手写请求表格数据
      getData:function(){
        var url = '/api/com/role/getRoleList';
        this.$http.get(url,{params:{
          page:this.page,
          size:this.size,
          name:this.roleName
        }}).then(function(res){
          //console.log(res);
          if(res.body.zt == 'true'){
            this.total = res.body.total;
            this.tableData = res.body.data;
          }
        });
      },
      //element-ui表格函数
      //点击行的时候获得当前行的ID值,以及行的内容
      clickRow:function(row, column, cell, event){
        //console.log(row);
        if(column.label == '角色名称'){
          this.dialogFormVisible = true;
          this.roleId = row.id;
          this.boxRoleName = row.name;//输入框input值
          this.textareas = row.descript;//文本域
          //console.log(this.roleId);
        }
      },
      toggleSelection:function(rows) {
        if (rows) {
          rows.forEach(function(){
            this.$refs.multipleTable.toggleRowSelection(row);
          });
        } else {
          this.$refs.multipleTable.clearSelection();
        }
      },
      handleSelectionChange:function(val) {
        //this.loading = false;
        this.multipleSelection = val;
        //console.log(val);
        //获得所有选中的ID值，拼接成字符串
        var ids = '';
        for(var i=0;i<val.length;i++){
          ids += val[i].id+',';
        }
        //console.log(ids);
        this.delRoleID = ids;
        //console.log(val);

        //获得最后选中项的name值，id值--/获取角色与菜单之间的关系
        var lastArr = val[val.length-1];
        if(val==''){
          //console.log('为空');
          this.filterText = '';
          this.filterId = 0;
          this.$refs.tree.setCheckedKeys([]);
        }else{
          this.$refs.tree.setCheckedKeys([]);
          this.checkedList = [];
          this.filterText = lastArr.name;
          this.filterId = lastArr.id;
          //console.log(this.filterText,this.filterId);
          var url = '/api/com/role/getChildPermission';
          this.$http.post(url,{rid:this.filterId},{emulateJSON:true}).then(function(res){
            this.loading = true;
            if(res.body.zt == 'true'){
              this.loading = false;
              //this.checkedList = res.body.data;
              this.$refs.tree.setCheckedNodes(res.body.data);
            }
          });
        }
      },
      //element-ui分页函数
      handleSizeChange:function(val) {
        //console.log(`每页 ${val} 条`);
        this.size = val;
        //展示数据
        this.getData();
      },
      handleCurrentChange:function(val) {
        //console.log(`当前页: ${val}`);
        this.page = val;
        //展示数据
        this.getData();
      }
    }
  }
</script>

<style scoped>
  #tmpl{
    margin-top: 60px;
  }
  #tmpl{
    text-align: left;
  }
  #searchGroup{
    text-align: left;
    height:62px;
    line-height: 50px;
  }
  .treeInput,
  .inputs{
    width:150px;
  }
</style>



