<template>
  <div id="tmpl">
    <!--<div class="loginGroup" v-loading="loading">
      <input type="text" v-model="userName"/>姓名
      <input type="text" v-model="passWord"/>密码
      <button @click="Login">登录</button>
    </div>-->
    <el-row class="logohead">
      <el-col :span="4"><div class="grid-content bg-purple titles"></div></el-col>
      <el-col :span="5">
        <div class="grid-content bg-purple titles">
          <img src="../../../static/loginImgs/logo.png" alt=""/>
        </div>
      </el-col>
      <el-col :span="15"><div class="grid-content bg-purple titles"></div></el-col>
    </el-row>


    <el-row :style="mainHeights" class="loginMain">
      <el-col :span="24"><div class="grid-content bg-purple main1s"></div></el-col>
   <!-- </el-row>
    <el-row :style="mainHeights">-->
      <el-col :span="4"><div class="grid-content bg-purple main2s"></div></el-col>
      <el-col :span="6">
        <div class="grid-content bg-purple main2s">
          <img src="../../../static/loginImgs/img_dl.png" alt=""/>
        </div>
      </el-col>
      <el-col :span="4"><div class="grid-content bg-purple main2s"></div></el-col>
      <el-col :span="6">
        <div class="grid-content bg-purple main2s">
          <div class="submitForm" style="box-sizing: border-box;padding:40px 50px 0px 50px;">
            <p style="margin:0;text-align: center;color:#5596f2;font-size: 20px;margin-bottom:30px;">登录</p>
            <el-input
              style="margin-bottom:20px"
              placeholder="请输入用户名"
              v-model="userName">
              <i slot="prefix" class="iconfont icon-denglu"></i>
            </el-input>

            <el-input
              type="password"
              style="margin-bottom:20px"
              placeholder="请输入密码"
              v-model="passWord">
              <i slot="prefix" class="iconfont icon-mima"></i>
            </el-input>

            <div style="text-align: left;margin-bottom:20px;">
              <span v-show="showOrHide == true" style="color:#f25555;font-size:14px;">用户名或密码错误</span>
              <span style="float:right;font-size:14px">
                <input type="checkbox"/>自动登录
              </span>
            </div>

            <el-button style="width:100%" type="primary" @click="Login">登录</el-button>
            <router-link to="" style="float:right;color:#5596f2;font-size:14px;margin-top:10px;">
              忘记密码
            </router-link>
          </div>
        </div>
      </el-col>
      <el-col :span="4"><div class="grid-content bg-purple main2s"></div></el-col>

      <el-col :span="24"><div style="height:50px" class="grid-content bg-purple"></div></el-col>

      <el-col :span="14"><div class="grid-content bg-purple footers"></div></el-col>
      <el-col :span="5">
        <div class="grid-content bg-purple footers">
          <img style="width:100%" src="../../../static/loginImgs/footer.png" alt=""/>
        </div>
      </el-col>
      <el-col :span="5"><div class="grid-content bg-purple footers"></div></el-col>
    </el-row>
  </div>
</template>
<script>
  export default {
    data:function(){
      return {
        //标志是否显示用户名密码错误
        showOrHide:false,
        mainHeights:{
          height:''
        },
        loading:false,
        userName:'',
        passWord:'',
        leftMenuList:[]
      }
    },
    created:function(){
      this.mainHeights.height = window.innerHeight-100+'px';
      let TOKEN = sessionStorage.getItem('token');
      //如果存在token，就为每次请求的header中设置好token，后台根据token判断
      if(TOKEN){
        let menus = JSON.parse(sessionStorage.getItem('menu'));
        let len = window.location.href.split('/').length;
        let logins = window.location.href.split('/')[len-1];
        //console.log(logins);
        if(logins == 'login'){
          this.$router.replace(menus[0].url);
          //console.log(menus);
        }
      }
    },
    methods:{
      Login:function(){
        this.loading = true;
        let params = {
          account:'PC-'+this.userName,
          password:this.passWord
        };
        //console.log(params);
        //console.log(this.passWord+' :密码， '+this.userName+' :名字');
        var url = '/api/login';
        this.$http.post(url,{
          userName:params.account,
          passWord:params.password
        }).then(function(res){
          //console.info('token:',res);
          sessionStorage.setItem('token',res.headers.map.authorization);
          var url = '/api/com/sys/getPermissionByUser';
          this.$http.post(url).then(function(res){
            //console.log(res);
            if(res.body.zt == 'true'){
              sessionStorage.setItem('USERNAME',params.account);
              this.leftMenuList = res.body.data;
              sessionStorage.setItem('menu',JSON.stringify(this.leftMenuList));
              this.$store.commit('MENU',this.leftMenuList);
              this.loading = false;
              this.$router.replace(this.leftMenuList[0].url);
              this.$message({
                showClose: true,
                message: '登录成功',
                type: 'success'
              });
              //console.log(this.leftMenuList);
            }
          });
        }).catch(function(err){
          this.userName = '';
          this.passWord = '';
          this.$message({
            showClose: true,
            message: '登录失败',
            type: 'default'
          });
          this.loading = false;
          this.showOrHide = true;
        });
      }
    }
  }
</script>
<style scoped>
  #app{
    margin-top:60px;
  }
  .footers{
    height:30px;
  }
  .submitForm>>>.el-input__prefix{
    top:12px;
    font-size:12px;
  }
  .submitForm{
    width:98%;
    height:98%;
    background-color: #fff;
  }
  .main2s img{
    width:98%;
  }
  .main2s{
    /*background-color: #b3e19d;*/
    height:360px;
  }
  .main1s{
    /*background-color: red;*/
    height:100px;
  }
  .loginMain{
    background-image: url('../../../static/loginImgs/bg_dl.png');
    background-size: 100% 100%;
  }
  .titles,
  .logohead{
    height:100px;
    background-color: #fff;
    margin-top:0;
    vertical-align: middle;
  }
  .titles img{
    margin-top:25px;
  }
  .loginGroup{
    box-sizing: border-box;
    padding:10px 40px;
    width:300px;
    height:200px;
    position:fixed;
    top:150px;
    left:40%;
    border:1px solid #0074D9;
    border-radius: 5px;
  }
  .loginGroup input{
    outline:none;
    border:1px solid #2b81af;
    margin-bottom:10px;
  }
</style>



