<template>
  <div id="tmpl">
    <div id="searchGroup">
      <el-input v-model="userName" class="inputs" placeholder="请输入角色名称" clearable size="small"></el-input>

      <el-button type="primary" size="small" @click="getUserList">查询</el-button>

      <el-button type="primary" size="small" @click="showBox">添加</el-button>
      <el-dialog
        @close="closeDialog"
        title="增加"
        :visible.sync="dialogFormVisible"
        :close-on-press-escape="false">
        <el-form>
          <el-form-item label="用户名" :label-width="formLabelWidth">
            <el-input v-model="addUserName" auto-complete="off" clearable></el-input>
          </el-form-item>
          <el-form-item label="密码" :label-width="formLabelWidth">
            <el-input v-model="addPassWord" auto-complete="off" clearable></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="closeDialog">取 消</el-button>
          <el-button type="primary" @click="addNewUser">确 定</el-button>
        </div>
      </el-dialog>

      <el-button type="primary" size="small" @click="delRows">删除</el-button>
      <div class="rightSearchGroup">
        <el-input
          class="treeInput"
          size="small"
          placeholder=""
          v-model="filterText">
        </el-input>
        <el-button type="primary" size="small" @click="getCheckedNodes">确定</el-button>
        <el-button type="primary" size="small" @click="choiceAll">全选</el-button>
      </div>
    </div>
    <el-row :gutter="10">
      <el-col :span="16">
        <div class="grid-content bg-purple">
          <el-table
            v-loading="loading"
            ref="multipleTable"
            :data="tableData"
            tooltip-effect="dark"
            style="width: 100%"
            @cell-click="rowClick"
            @selection-change="handleSelectionChange">
            <el-table-column
              type="selection"
              width="55">
            </el-table-column>
            <el-table-column
              prop="userName"
              label="姓名">
              <template slot-scope="scope">
                <span style="color:#09f;cursor: pointer">{{ scope.row.userName }}</span>
              </template>
            </el-table-column>
            <el-table-column
              prop="passWord"
              label="密码"
              show-overflow-tooltip>
            </el-table-column>
          </el-table>
          <div style="text-align: center">
            <el-pagination
              @size-change="handleSizeChange"
              @current-change="handleCurrentChange"
              :current-page="page"
              :page-sizes="[5, 10, 15, 20]"
              :page-size="size"
              layout="total, sizes, prev, pager, next, jumper"
              :total="total">
            </el-pagination>
          </div>
        </div>
      </el-col>
      <el-col :span="8">
        <div class="grid-content bg-purple">
          <el-tree
            v-loading="loading2"
            :data="treeData"
            show-checkbox
            default-expand-all
            node-key="id"
            ref="tree"
            :props="defaultProps">
          </el-tree>
        </div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
  export default {
    data:function(){
      return {
        loading:true,
        loading2:false,
        //全选全不选
        //右侧表格的最大高度
        maxHeight:0,
        //删除数据的行ID
        delIds:'',
        //数据行的ID值
        rowID:'',
        //初始化弹出框数据
        addUserName:'',
        addPassWord:'',
        formLabelWidth: '120px',
        //弹出框的显示隐藏
        dialogFormVisible:false,
        //页面开头查询用户的输入框
        userName:'',
        //分页数据
        page:1,//当前页数
        size:5,//每页显示的行数
        total:0,//移动多少条数据
        //表格数据
        tableData:[],
        //右侧表格的数据
        rightTableDataList:[],
        //右侧表格选中的id值集合
        rightTableIds:'',
        //右侧表格回显的数据
        rightReturnList:[],
        //树形控件的数据
        checkedList:[],//默认选中的值
        filterText: '',
        filterId:0,
        treeData:[],
        defaultProps: {
          children: 'children',
          label: 'label'
        }
      }
    },
    created:function(){
      this.maxHeight = window.innerHeight-80-62-30;
      //展示数据
      this.getUserList();
      //右侧表格展示数据
      this.getTreeData();
    },
    methods:{

      //树形控件全选
      choiceAll:function(){
        this.treeClickNum = !this.treeClickNum;
        if(!this.treeClickNum){
          this.$refs.tree.setCheckedNodes([]);
        }else{
          this.$refs.tree.setCheckedNodes(this.treeData);
        }
      },
      //新增用户与角色之间的关系===========================
      getCheckedNodes:function(){
        //console.log(this.delRoleID);
        var arr = this.delIds.split(',');
        //console.log(arr.length,arr,this.delRoleID);
        if(arr.length>2){
          this.$message({
            showClose: true,
            type: 'warning',
            message: '最多只能选择一个角色！'
          });
        }else if(arr.length<2){
          this.$message({
            showClose: true,
            type: 'warning',
            message: '请选择一个角色！'
          });
        }else{
          var rids = this.$refs.tree.getCheckedNodes(true,false);
          var rid = '';
          for(var i=0;i<rids.length;i++){
            rid += rids[i].id+',';
          }
          //console.log(arr[0]+' :表格ID');
          var arrs = arr[0]*1;
          //console.log(arrs);
          //console.log(this.delRoleID+' :表格中的ID');
          //console.log(rid+' :树节点中的ID');
          var url = '/api/com/user/insertUserAndRole';
          this.$http.post(url,{
            uid:arrs,
            ids:rid
          },{emulateJSON:true}).then(function(res){
            //console.log(res);
            if(res.body.zt == 'true'){
              this.$message({
                showClose: true,
                type: 'success',
                message: '角色设置成功！'
              });
            }
          });
        }
      },
      //右侧列表的数据请求==============================
      //树tree展示数据
      getTreeData:function(){
        this.loading2 = true;
        var url = '/api/com/user/getAllRole';
        this.$http.post(url,{emulateJSON:true}).then(function(res){
          //console.log(res);
          if(res.body.zt == 'true'){
            //console.log(res);
            this.treeData = res.body.data;
            this.loading2 = false;
          }
        });
      },
      //删除行的函数==========================
      delRows:function(){
        var del = confirm('确定删除选中行？');
        if(del == true){
          var url = '/api/com/user/deleteUserById';
          this.$http.post(url,{ids:this.delIds},{emulateJSON:true}).then(function(res){
            if(res.body.zt == 'true'){
              this.$message({
                showClose: true,
                type: 'success',
                message: '删除成功!'
              });
              //展示数据
              this.getUserList();
            }
          });
        }else{
          this.$message({
            showClose: true,
            type: 'warning',
            message: '您已经取消删除!'
          });
        }
      },
      //关闭弹框后清空所有项=============================
      closeDialog:function(){
        this.dialogFormVisible = false;
        this.rowID = '';
        //初始化弹出框数据
        this.addUserName = '';
        this.addPassWord = '';
      },
      //点击表格的行，获取ID值==============================
      rowClick:function(row, column, cell, event){
        if(column.label == '姓名'){
          //console.log(row);
          this.rowID = row.id;
          //初始化弹出框数据
          this.addUserName = row.userName;
          this.addPassWord = row.passWord;
          this.dialogFormVisible = true;
        }
      },
      //新增、修改用户============================
      addNewUser:function(){
        this.dialogFormVisible = false;
        var url = '/api/com/user/insertUser';
        if(this.rowID == ''){
          this.rowID = 0;
        }
        this.$http.post(url,{
          id:this.rowID,
          userName:this.addUserName,
          passWord:this.addPassWord
        },{emulateJSON:true}).then(function(res){
          if(res.body.zt == 'true'){
            if(this.rowID == ''){
              this.$message({
                showClose: true,
                type: 'success',
                message: '添加成功!'
              });
            }else{
              this.$message({
                showClose: true,
                type: 'success',
                message: '修改成功!'
              });
            }
            //展示数据
            this.getUserList();
          }
        });
      },
      //弹出框的显示隐藏=========================
      showBox:function(){
        this.dialogFormVisible = true;
      },
      //获取表格数据=========================
      getUserList:function(){
        var url = '/api/com/user/getUserList';
        this.$http.get(url,{params:{
          name:this.userName,
          page:this.page,
          size:this.size
        }}).then(function(res){
          //console.log(res);
          if(res.body.zt == 'true'){
            this.tableData = res.body.data;
            this.total = res.body.total;
            //console.log(this.total);
            this.loading = false;
          }
        });
      },
      //element-ui表格函数====================
      toggleSelection:function(rows){
        if (rows) {
          rows.forEach(function(row) {
            this.$refs.multipleTable.toggleRowSelection(row);
          });
        } else {
          this.$refs.multipleTable.clearSelection();
        }
      },
      handleSelectionChange:function(val) {
        this.loading2 = true;
        //获得左侧点击复选框的选中项ID值集合==================
        this.multipleSelection = val;
        //console.log(val);
        var ids = '';
        for(var i=0;i<val.length;i++){
          ids += val[i].id+',';
        }
        this.delIds = ids;
        //console.log(ids);


        //回显用户的权限=============================
        var lastArr = val[val.length-1];
        //console.log(lastArr);
        if(val==''){
          //console.log('为空');
          this.filterText = '';
          this.filterId = 0;
          this.$refs.tree.setCheckedKeys([]);
        }else{
          this.$refs.tree.setCheckedKeys([]);
          this.checkedList = [];
          this.filterText = lastArr.userName;
          this.filterId = lastArr.id;
          //console.log(this.filterId);
          //console.log(this.filterText,this.filterId);
          var url = '/api/com/user/getRoleAndUserByUid';
          this.$http.post(url,{uid:this.filterId},{emulateJSON:true}).then(function(res){
            //console.log(res);
            if(res.body.zt == 'true'){
              //this.checkedList = res.body.data;
              this.$refs.tree.setCheckedNodes(res.body.data);
              this.loading2 = false;
            }
          });
        }
      },
      //element-ui分页函数=======================
      handleSizeChange:function(val) {
        this.loading2 = false;
       // console.log(`每页 ${val} 条`);
        this.size = val;
        //展示数据
        this.getUserList();
      },
      handleCurrentChange:function(val) {
        this.loading2 = false;
        //console.log(`当前页: ${val}`);
        this.page = val;
        //展示数据
        this.getUserList();
      }
    }
  }
</script>

<style scoped>
  #tmpl{
    margin-top: 60px;
  }
  #tmpl{
    text-align: left;
  }
  #searchGroup{
    text-align: left;
    height:62px;
    line-height: 50px;
  }
  .inputs{
    width:150px;
  }
  .treeInput{
    width:150px;
  }
  .rightSearchGroup{
    width:290px;
    height:50px;
    float:right;
  }
  .choiceAll{
    display: inline-block;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    height:48px;
    width:100%;
    border-bottom: 1px solid #ddd;
    padding:14px 0;
  }
  ul{
    margin:0;
    padding:0;
  }
  li{
    box-sizing: border-box;
    height:48px;
    border-bottom:1px solid #ddd;
    padding:14px 0;
  }
</style>



