<!--患者准入评估页面-->
<template>
  <div id="tmpl">
    <div class="searchGroup">
      <el-input size="small" v-model="opeName" class="searchInput" placeholder="请输入要查询患者名" clearable></el-input>
      <el-button type="primary" size="small" @click="searchClick">查询</el-button>
      <div class="rightBtns">
        <el-button type="primary" size="small" @click="getOpe">可日间手术</el-button>
        <el-button type="primary" size="small" @click="noGetOpe">不可日间手术</el-button>
        <el-button type="primary" size="small" @click="getOther">等待其他结果</el-button>
        <el-button type="primary" size="small" @click="refreshTable">刷新</el-button>
      </div>
    </div>

<!--  --------  表格展示数据   ----------  -->

    <el-table
      @cell-click="clickCell"
      :data="tableData"
      border
      style="width: 100%">
      <el-table-column
        fixed
        align="center"
        prop="patientName"
        min-width="50px"
        label="姓名">
        <template slot-scope="scope">
          <span style="color:#09f;cursor: pointer">{{ scope.row.patientName }}</span>
        </template>
      </el-table-column>
      <el-table-column
        align="center"
        prop="age"
        min-width="50px"
        label="年龄">
      </el-table-column>
      <el-table-column
        align="center"
        prop="operationName"
        min-width="150px"
        label="手术名称">
      </el-table-column>
      <el-table-column
        align="center"
        prop="operationType"
        min-width="80px"
        label="检验结果">
      </el-table-column>
      <el-table-column
        align="center"
        prop="evaluateTime"
        min-width="80px"
        label="评估时间">
      </el-table-column>
      <el-table-column
        fixed="right"
        min-width="100px"
        align="center"
        prop=""
        label="">
        <template slot-scope="scope">
          <span class="groupsBtn">检验结果</span>
          <span class="groupsBtn">检验报告</span>
          <span class="groupsBtn">术前评估</span>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="currentPage"
      :page-sizes="[5, 10, 15, 20]"
      :page-size="size"
      layout="total, sizes, prev, pager, next, jumper"
      :total="total">
    </el-pagination>

    <!--  ------------------  表格弹出框  ----------------  -->
    <el-dialog class="tanchuBox" title="" :visible.sync="dialogFormVisible" @close="outForm">
      <div>
        <el-form :model="form">
          <el-form-item label="患者信息" :label-width="formLabelWidth">
            <ul>
              <li>
                <b>患者：</b><span style="margin-right:20px" v-text="form.patientName"></span>
                <b>年龄：</b><span v-text="form.patientAge"></span>
              </li>
              <li>
                <b>手术：</b><span v-text="form.opeNames"></span>
              </li>
            </ul>
          </el-form-item>
          <el-form-item label="检验结果" :label-width="formLabelWidth">
            <el-radio v-model="form.radios" label="0">可日间手术</el-radio>
            <el-radio v-model="form.radios" label="1">不可日间手术</el-radio>
            <el-radio v-model="form.radios" label="2">等待其他结果</el-radio>
          </el-form-item>

          <el-form-item label="快速留言" :label-width="formLabelWidth">
            <el-checkbox-group v-model="form.labels" @change="handleCheck">
              <el-checkbox class="checkboxList" v-for="item in form.fastList" :key="item.id" :label="item.mid">{{ item.message }}</el-checkbox>
              <br/>
            </el-checkbox-group>
          </el-form-item>

          <el-form-item label="留言" :label-width="formLabelWidth">
            <el-input
              class="tanchuTextarea"
              type="textarea"
              :rows="2"
              placeholder="请输入留言内容"
              v-model="form.textarea">
            </el-input>
            <el-button type="primary" size="small" @click="saveDialog">保存</el-button>
          </el-form-item>
          <el-form-item label="留言列表" :label-width="formLabelWidth">
            <div class="liuyanMessage">
              <ul>
                <!--<li v-if="">您暂时没有留言</li>-->
                <li class="liuyanLi" v-for="item in form.liuyanList" :key="item.id">
                  <span class="messageList" style="" :title="item.message">{{ item.message }}</span>
                  <span>{{ item.messageDate }}</span>
                </li>
              </ul>
            </div>
          </el-form-item>
        </el-form>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button @click="outForm" size="small">退出</el-button>
        <!--<el-button type="primary" @click="dialogFormVisible = false" size="small">确 定</el-button>-->
      </div>
    </el-dialog>
  </div>
</template>

<script>
  export default {
    data:function(){
      return {
        //弹出框数据
        patientId:0,//点击弹框的病人ID值
        dialogFormVisible: false,
        form: {
          //表格回写数据
          patientName:'',//患者名
          patientAge:0,//患者年龄
          opeNames:'',//患者手术名
          radios:'',//是否可以日间手术的单选框
          liuyanList:[],//留言列表数据
          fastList:[],//快速留言列表
          labels:[],//快速留言列表的value值
          strLabels:'',//拼接后的快速留言列表的value值


          textarea:'',
          checkList:[],
          name: '',
          region: '',
          date1: '',
          date2: '',
          delivery: false,
          type: [],
          resource: '',
          desc: ''
        },
        formLabelWidth: '120px',


        //表格查询框的默认值
        opeName:'',
        //表格数据
        tableData: [],
        //分页数据
        currentPage:1,//默认展示第一页
        size:5,//默认每页显示五条数据
        total:0,//总数据的个数,
        nums:3
      }
    },
    created:function(){
      //展示数据
      this.getTableList();
    },
    mounted:function(){},
    methods:{
      getOpe:function(){
        this.nums = 0;
        this.getTableList();
      },
      noGetOpe:function(){
        this.nums = 1;
        this.getTableList();
      },
      getOther:function(){
        this.nums = 2;
        this.getTableList();
      },
      refreshTable:function(){
        this.nums = 3;
        this.getTableList();
      },
      outForm:function(){
          this.dialogFormVisible = false;
          //清空form表单
          this.form.textarea = '';
          this.form.labels = [];
          this.form.radios = '';
      },
      //获得快速留言列表的id值
      handleCheck:function(val){
        //console.log(val);
        var ids = '';
        for(var i=0;i<val.length;i++){
          ids += val[i]+',';
        }
        //console.log(ids);
        this.form.strLabels = ids
      },
      //保存弹出框中的内容
      saveDialog:function(){
        //console.log('fdsa');
        //console.log(this.form.textarea);
        var url = '/api/com/evaluate/insertPatientEvaluate';
        this.$http.post(url,{
          operationType:this.form.radios,
          id:this.patientId,
          fastMessage:this.form.strLabels,
          message:this.form.textarea
        },{emulateJSON:true}).then(function(res){
          //console.log(res);
          //this.form.liuyanList = res.body.data;
          if(res.body.zt == 'true'){
            this.form.liuyanList = res.body.data;
            this.$message({
              showClose: true,
              message: '保存成功',
              type: 'success'
            });

            //清空form表单
            this.form.textarea = '';
            this.form.labels = [];
            this.form.radios = '';
            //console.log(this.form);
          }else{
            this.$message({
              showClose: true,
              message: '保存失败',
              type: 'error'
            });
          }
        });
      },


      //--------------弹框展示详情和留言列表-------------
      //点击弹出框显示
      clickCell:function(row, column, cell, event){
        this.patientId = row.id;
        //console.log(this.patientId);
        //console.log(column.label);
        if(column.label == '姓名'){
          var url = '/api/com/evaluate/getPatientEvaluateById';
          this.$http.post(url,{
            id:row.id,
            name:row.patientName
          },{emulateJSON:true}).then(function(res){
            if(res.body.zt == 'true'){
              this.dialogFormVisible = true;
              //console.log(res);
              this.form.patientName = res.body.data.patientName;//患者名
              this.form.patientAge = res.body.data.age;//患者年龄
              this.form.opeNames = res.body.data.operationName;//患者手术名
              this.form.radios = res.body.data.operationType+'';//是否可以日间手术的单选框
              this.form.liuyanList = res.body.data.leaveMessage;//留言列表数据
              this.form.fastList = res.body.data.fastMessage;//快速留言列表数据
             // console.log(this.form.fastList);
            }
          });
        }
      },


      //---------------初始化页面------------------
      //点击查询按钮获得查询结果
      searchClick:function(){
        this.getTableList();
      },
      //手动获取表格数据
      getTableList:function(){
        //console.log(this.opeName);
        var url = '/api/com/evaluate/getPatientEvaluateList';
        this.$http.post(url,{
          page:this.currentPage,
          size:this.size,
          name:this.opeName,
          operationType:this.nums
        },{emulateJSON:true}).then(function(res){
            console.log(res);
          if(res.body.zt == 'true'){
            this.tableData = res.body.data;
            this.total = res.body.total;
          }
        });
      },
      //分页方法
      handleSizeChange:function(val) {
        //console.log(`每页 ${val} 条`);
        this.size = val;
        //展示数据
        this.getTableList();
      },
      handleCurrentChange:function(val) {
        //console.log(`当前页: ${val}`);
        this.currentPage = val;
        //展示数据
        this.getTableList();
      }
    }
  }
</script>

<style scoped>
  #tmpl{
    margin-top: 60px;
  }
  .checkboxList{
    margin:0;
  }
  .liuyanLi{
    height:40px;
    line-height: 40px;;
  }
  .liuyanLi span{
    display: inline-block;
    height:40px;
    line-height: 40px;
    vertical-align: middle;
  }
  .messageList{
    display: inline-block;
    margin-right:20px;
    width:60%;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
  }
  .liuyanMessage{
    max-height:150px;
    overflow: auto;
  }
  .groupsBtn{
    background-color: #09f;
    color:#fff;
    cursor:pointer;
  }

.tanchuTextarea{
  width:80%;
}
.el-dialog__header{
  display: none;
}
.searchGroup{
  text-align: left;
  height:62px;
}
.searchInput{
  width:150px;
  margin-top:8px;
}
.rightBtns{
  float:right;
  margin-top:8px;
}
.tanchuBox{
  text-align: left;
}
</style>



