<template>
  <div id="tmpl">
    <div id="mubu" v-show="showOrHiddenMubu==1"></div>
    <div class="title">
      <span>{{ opeInformation.operationName }}&nbsp;&nbsp;{{ opeInformation.opetionCode }}</span>
      <div>
        <div class="upload-btn uploadDiv" @click="showTankuang(1)">上传宣教文件</div>
        <div class="upload-btn uploadDiv" @click="showTankuang(2)">上传出院指导文件</div>
        <div style="width:50px" class="upload-btn uploadDiv" @click="goBack">返回</div>

        <div class="uploadParent" v-show="showOrHidden==1">
          <div class="closeTip" @click="closeTankuang">×</div>
          <div class="uploadBtnParent">
            <a class="a-upload">
              上传宣教文件<i class="el-icon-upload el-icon--right"></i>
              <input type="file" @change="getFile($event)" />
            </a>
            <button @click="upload" class="upload-btn">上传</button>
            <div class="fileNames" style="width:100px;">{{ result }}</div>
            <div v-show="uping==1">{{ fileNames }}正在上传中...</div>
          </div>
          <p>上传历史</p>
          <div class="tableParent">
            <table>
              <tr v-for="item in fileHistoryList">
                <td>
                  <span>{{ item.fileName }}</span>
                  <span v-show="false">{{ item.fileId }}文件ID</span>
                  <span v-show="false">{{ item.fileUrl }}文件URL</span>
                  <span v-show="false">{{ item.operationId }}手术ID</span>
                  <span v-show="false">{{ item.fileType }}点击按钮类型</span>
                </td>
                <td>
                  <button class="upload-btn" @click="delHistoryList(item.operationId,item.fileId,item.fileType)">删除</button>
                </td>
                <td>
                  <button class="upload-btn" @click="saveHistoryFile(item.fileId)">下载</button>
                </td>
              </tr>
            </table>
          </div>
        </div>

        <div class="uploadParent" v-show="showOrHidden==2">
          <div class="closeTip" @click="closeTankuang">×</div>
          <div class="uploadBtnParent">
            <a class="a-upload">
              上传出院指导文件<i class="el-icon-upload el-icon&#45;&#45;right"></i>
              <input type="file" @change="getFile2($event)" />
            </a>
            <button @click="upload2" class="upload-btn">上传</button>
            <div>{{ result2 }}</div>
            <div v-show="uping2==1">{{ fileNames }}正在上传中...</div>
          </div>
          <p>上传历史</p>
          <div class="tableParent">
            <table>
              <tr v-for="item in fileHistoryList">
                <td>
                  <span>{{ item.fileName }}</span>
                  <span v-show="false">{{ item.fileUrl }}文件URL</span>
                  <span v-show="false">{{ item.operationId }}手术ID</span>
                  <span v-show="false">{{ item.fileType }}点击按钮类型</span>
                </td>
                <td>
                  <button class="upload-btn" @click="delHistoryList(item.operationId,item.fileId,item.fileType)">删除</button>
                </td>
                <td>
                  <button class="upload-btn" @click="saveHistoryFile(item.fileId)">下载</button>
                </td>
              </tr>
            </table>
          </div>
        </div>
        <!--<el-button @click="goBack" type="primary" size="small">返回</el-button>
        <el-popover
          width="500"
          placement="left"
          trigger="click">
          <el-upload
            class="upload1"
            :action="actions()"
            :before-upload="beforeUpLoad"
            multiple
            :show-file-list="false"
            :limit="1"
            :data="ppss"
            :on-error="uploadError"
            :on-success="uploadSuccess"
            :file-list="fileList">
            <el-button size="small" type="primary">上传新文件<i class="el-icon-upload el-icon&#45;&#45;right"></i></el-button>
          </el-upload>
          <ul id="ulParent">
            <li>上传历史</li>
            <li v-for="item in upLoadList">{{ item }}</li>
          </ul>
          <el-button slot="reference" size="small" type="primary">上传宣教文件<i class="el-icon-upload el-icon&#45;&#45;right"></i></el-button>
        </el-popover>

        <el-popover
          width="500"
          placement="left"
          trigger="click">
          <el-upload
            class="upload2"
            :action="actions2()"
            :before-upload="beforeUpLoad2"
            multiple
            :show-file-list="true"
            :limit="3"
            :data="ppss"
            :on-error="uploadError"
            :on-success="uploadSuccess"
            :file-list="fileList">
            <el-button size="small" type="primary">上传新文件<i class="el-icon-upload el-icon&#45;&#45;right"></i></el-button>
          </el-upload>
          <ul id="ulParent2">
            <li>上传历史</li>
            <li>1111</li>
            <li>1111</li>
            <li>wwwwwwww.docx</li>
          </ul>
          <el-button slot="reference" size="small" type="primary">上传出院指导文件<i class="el-icon-upload el-icon&#45;&#45;right"></i></el-button>
        </el-popover>-->
      </div>
    </div>
    <div>
      <p>病例相关知识指导</p>
      <br/>
      <textarea v-model="opeInformation.illGuid"></textarea>
    </div>
    <div>
      <p>用药指导</p>
      <br/>
      <textarea v-model="opeInformation.medicineGuid"></textarea>
    </div>
    <div>
      <p>饮食指导</p>
      <br/>
      <textarea v-model="opeInformation.foodGuid"></textarea>
    </div>
    <div>
      <p>心理指导</p>
      <br/>
      <textarea v-model="opeInformation.mindGuid"></textarea>
    </div>
    <div>
      <p>术日安排</p>
      <br/>
      <textarea v-model="opeInformation.operationPlan" class="succ"></textarea>
    </div>
    <el-button @click="getTextareaInfo" type="primary" size="medium" id="saveBtn">保存</el-button>
  </div>
</template>

<script>
  export default {
    data:function(){
      return {
        //控制弹框和幕布的显示隐藏
        showOrHidden:0,
        showOrHiddenMubu:0,
        //上传文件2018-7-29
        upath: '',
        result: '',
        uping:0,
        upath2: '',
        result2: '',
        uping2:0,
        fileNames:'',

        fileHistoryList:[],
        //fileList:[],
        opeInformation:[{
          operationName:'手术名称',
          opetionCode:'手术编码',
          illGuid:'病例相关知识指导',
          medicineGuid:'用药指导',
          foodGuid:'饮食指导',
          mindGuid:'心理指导',
          operationPlan:'术日安排',
          operationId:0
        }],
        fileType:0,
        upLoadList:[],
        loading2: true
      }
    },
    created:function(){

    },
    mounted:function(){
      this.getUL();
    },
    methods:{
      //加载中的遮罩层
      //下载文件
      saveHistoryFile:function(fId){
        var url = '/api/com/operation/downLoadOperationFile';
        this.$http.get(url,{params:{fId:fId}}).then(function(res){
          window.location.href = url+'?fId='+fId;
        });
      },

      //删除历史文件
      delHistoryList:function(opeId,fileId,fileType){
        //console.log(opeId,fileId,fileType);
        var url = '/api/com/operation/deleteFileById';
        this.$http.get(url,{params:{fId:fileId,opeId:opeId,fileType:fileType}}).then(function(res){
            if(res.body.zt === 'true'){

              alert('删除成功');
              this.getUL();
            }
        });
      },
      //弹框和幕布的显示隐藏
      showTankuang:function(num){
        this.showOrHiddenMubu = 1;
        if(num == 1){
          this.showOrHidden = 1;
          this.fileHistoryList = this.opeInformation.xgFileList;
         //console.log(this.fileHistoryList);
        }
        if(num == 2){
          this.showOrHidden = 2;
          this.fileHistoryList = this.opeInformation.zdFileList;
          //console.log(this.fileHistoryList);
        }
      },
      closeTankuang:function(){
        this.showOrHidden = 0;
        this.showOrHiddenMubu = 0;
      },
      //上传文件2018-7-29
      upload: function () {
        this.showOrHidden = 0;
        this.showOrHiddenMubu = 0;
        const loading = this.$loading({
        lock: true,
        text: '正在上传...',
        spinner: 'el-icon-loading',
        background: 'rgba(0, 0, 0, 0.7)'
      });
        //console.log(this.upath);
        this.fileNames = this.upath.name;
        const extension = this.upath.name.split('.')[1] === 'xls';
        const extension2 = this.upath.name.split('.')[1] === 'xlsx';
        const extension3 = this.upath.name.split('.')[1] === 'doc';
        const extension4 = this.upath.name.split('.')[1] === 'docx';
        const extension5 = this.upath.name.split('.')[1] === 'mp4';
        const extension6 = this.upath.name.split('.')[1] === 'rmvb';
        const extension11 = this.upath.name.split('.')[1] === 'avi';
        const extension12 = this.upath.name.split('.')[1] === 'wmv';
        const extension13 = this.upath.name.split('.')[1] === 'flv';
        const extension14 = this.upath.name.split('.')[1] === '3gp';
        const extension7 = this.upath.name.split('.')[1] === 'jpg';
        const extension8 = this.upath.name.split('.')[1] === 'png';
        const extension9 = this.upath.name.split('.')[1] === 'ppt';
        const extension10 = this.upath.name.split('.')[1] === 'pptx';
        const isLt2M = this.upath.size / 1024 / 1024 < 500;
        if (!extension && !extension2 && !extension3 && !extension4 && !extension5 && !extension6 && !extension7 && !extension8 && !extension9 && !extension10 && !extension11 && !extension12 && !extension13 && !extension14) {
          this.$message({
            showClose: true,
            message: '上传文件只能是 xls、xlsx、doc、docx、mp4、rmvb、jpg、png、ppt、pptx 格式!',
            type: 'success'
          });
          //console.log('上传文件只能是 xls、xlsx、doc、docx、mp4、rmvb 格式!');
          return false;
        }else if (!isLt2M) {
          //console.log('上传模板大小不能超过 10MB!');
          this.$message({
            showClose: true,
            message: '上传文件大小不能超过 500MB!',
            type: 'success'
          });
          return false;
        }else{
          var zipFormData = new FormData();
          this.fileType = 1;
          var fileType =  this.fileType;
          zipFormData.append('file', this.upath);//filename是键，file是值，就是要传的文件，test.zip是要传的文件名
          zipFormData.append('opeId', this.opeInformation.operationId);//filename是键，file是值，就是要传的文件，test.zip是要传的文件名
          zipFormData.append('fileType', fileType);
          let config = { headers: { 'Content-Type': 'multipart/form-data' } };
          this.uping = 1;
          this.$http.post('/api/com/operation/uploadOperationFile', zipFormData,config).then(function (res) {
            /* console.log(res);
             console.log(res.data);
             console.log(res.bodyText);*/
            var resultobj = res.data;
            this.uping = 0;
            this.result = resultobj.msg;
            this.fileNames = res.name;
            this.$message({
              showClose: true,
              message: '上传成功',
              type: 'success'
            });
            this.showTankuang(1);
            loading.close();
          });
        }

      },
      upload2: function () {
        console.log('进方法');
        this.showOrHidden = 0;
        this.showOrHiddenMubu = 0;
        const loading = this.$loading({
          lock: true,
          text: '正在上传...',
          spinner: 'el-icon-loading',
          background: 'rgba(0, 0, 0, 0.7)'
        });
        //console.log(this.upath);
        this.fileNames = this.upath.name;
        const extension = this.upath2.name.split('.')[1] === 'xls';
        const extension2 = this.upath2.name.split('.')[1] === 'xlsx';
        const extension3 = this.upath2.name.split('.')[1] === 'doc';
        const extension4 = this.upath2.name.split('.')[1] === 'docx';
        const extension5 = this.upath2.name.split('.')[1] === 'mp4';
        const extension6 = this.upath2.name.split('.')[1] === 'rmvb';
        const extension11 = this.upath2.name.split('.')[1] === 'avi';
        const extension12 = this.upath2.name.split('.')[1] === 'wmv';
        const extension13 = this.upath2.name.split('.')[1] === 'flv';
        const extension14 = this.upath2.name.split('.')[1] === '3gp';
        const extension7 = this.upath2.name.split('.')[1] === 'jpg';
        const extension8 = this.upath2.name.split('.')[1] === 'png';
        const extension9 = this.upath2.name.split('.')[1] === 'ppt';
        const extension10 = this.upath2.name.split('.')[1] === 'pptx';
        const isLt2M = this.upath2.size / 1024 / 1024 < 500;
        if (!extension && !extension2 && !extension3 && !extension4 && !extension5 && !extension6 && !extension7 && !extension8 && !extension9 && !extension10 && !extension11 && !extension12 && !extension13 && !extension14) {
          this.$message({
            showClose: true,
            message: '上传文件只能是 xls、xlsx、doc、docx、mp4、rmvb、jpg、png、ppt、pptx 格式!',
            type: 'success'
          });
          //console.log('上传文件只能是 xls、xlsx、doc、docx、mp4、rmvb 格式!');
          return false;
        }else if (!isLt2M) {
          //console.log('上传模板大小不能超过 10MB!');
          this.$message({
            showClose: true,
            message: '上传文件大小不能超过 500MB!',
            type: 'success'
          });
          return false;
        }else{
          var zipFormData = new FormData();
          this.fileType = 2;
          var fileType =  this.fileType;
          zipFormData.append('file', this.upath2);//filename是键，file是值，就是要传的文件，test.zip是要传的文件名
          zipFormData.append('opeId', this.opeInformation.operationId);//filename是键，file是值，就是要传的文件，test.zip是要传的文件名
          zipFormData.append('fileType', fileType);//filename是键，file是值，就是要传的文件，test.zip是要传的文件名
          let config = { headers: { 'Content-Type': 'multipart/form-data' } };
          this.uping2 = 1;
          this.$http.post('/api/com/operation/uploadOperationFile', zipFormData,config).then(function (res) {
            /* console.log(res);
             console.log(res.data);
             console.log(res.bodyText);*/
            if(res.body.zt === 'true'){
              var resultobj = res.data;
              this.uping2 = 0;
              this.result2 = resultobj.msg;
              this.$message({
                showClose: true,
                message: '上传成功',
                type: 'success'
              });
              this.showTankuang(2);
              loading.close();
            }else{
              this.$message({
                showClose: true,
                message: '上传失败',
                type: 'error'
              });
            }
          });
        }
      },

      getFile: function (even) {
        this.upath = event.target.files[0];

      },
      getFile2: function (even) {
        this.upath2 = event.target.files[0];
      },
      //返回上一级页面
      goBack:function(){
        window.history.go(-1);
      },
      getUL:function(){
        //console.log(this.opeInformation);
        if(this.$route.params.data){
          this.opeInformation = this.$route.params.data;
        }else{
          this.opeInformation = {
            operationName:'手术名称',
            opetionCode:'手术编码',
            illGuid:'填写病例相关知识指导',
            medicineGuid:'填写用药指导',
            foodGuid:'填写饮食指导',
            mindGuid:'填写心理指导',
            operationPlan:'填写术日安排'
          };
        }
      },
      getTextareaInfo:function(){
        var oId = this.opeInformation.operationId;
        var illGuid = this.opeInformation.illGuid;
        var medicineGuid = this.opeInformation.medicineGuid;
        var foodGuid = this.opeInformation.foodGuid;
        var mindGuid = this.opeInformation.mindGuid;
        var operationPlan = this.opeInformation.operationPlan;
        var url = '/api/com/operation/insertOperationEducation';
        this.$http.get(url,
          {params:
            {
              oId:oId,
              illGuid:illGuid,
              medicineGuid:medicineGuid,
              foodGuid:foodGuid,
              mindGuid:mindGuid,
              operationPlan:operationPlan
            }}).then(function(res){
          //console.log(res.body);
          if(res.body.zt === 'true'){
            //console.log(res);
            this.$message({
              showClose: true,
              message: '操作成功',
              type: 'success'
            });
          }else{
            this.$message({
              showClose: true,
              message: '操作失败',
              type: 'error'
            });
          }
        });
      }
    }
  }
</script>

