    background-color: rgba(0, 0, 0, 0.3);
  }
  .uploadDiv{
    width:120px;
    text-align: center;
    float:left;
    margin-right:6px;
  }
#saveBtn{
  float:none;
}
#ulParent,#ulParent2{
  float:right;
  margin-right:10px;
}
#ulParent2 li,
#ulParent li{
  height:30px;
  line-height: 30px;
}
#ulParent2 li:first-child,
#ulParent li:first-child{
  color:red;
