  width:100%;
  text-align: left;
}
#tmpl>div>textarea{
  resize:none;
  border:1px solid #ccc;
  border-top:2px solid #787878;
  width:100%;
  height:60px;
  outline:none;
  box-sizing: border-box;
  padding:6px;
}
#tmpl>div>.succ{
  height:136px;
}
#tmpl>.title{
  text-align: left;
  width:100%;
  height:50px;
}
#tmpl>.title>div{
  float:right;
  height:80px;
}
#tmpl>.title>span{
  display: inline-block;
  height:50px;
  line-height:50px;
}
button{
  float:right;
  margin-right:20px;
}
  /*button{
    box-sizing: border-box;
    padding:6px 16px;
    margin:10px 10px;
    border-radius: 5px;
    border:none;
    outline:none;
    color:#fff;
    cursor:pointer;
    background-color: #0059b3;
  }*/
.el-message,.el-message--success,.is-closable{
  z-index:10000;
}
.uploadFile1{
  display:inline-block;
  width:180px;
  margin-right:40px;
}
.uploadFile3>.el-upload-list,
.uploadFile2>.el-upload-list,
.uploadFile1>.el-upload-list{
  height:100px;
}
.uploadFile2{
  display:inline-block;
  width:180px;
  margin-right:40px;
}
.uploadFile3{
  display:inline-block;
  width:180px;
  margin-right:40px;
}
#tmpl>.title>div>div{
  vertical-align: top;
}
#tmpl>.title>div>div>div>button{
  vertical-align: top;
}
.el-upload-list__item.is-success:focus:not(:hover) .el-icon-close-tip{
  display:none!important;
}
.upload-btn{
  font-size: 14px;
  padding: 4px 10px;
  height: 28px;
  line-height: 22px;
  border:none;
  border-radius: 5px;
  cursor: pointer;
  color: #fff;
  background-color: #409EFF;
  margin-left:10px;
}
.a-upload {
  font-size: 14px;
  padding: 4px 10px;
  height: 20px;
  line-height: 20px;
  position: relative;
  cursor: pointer;
  color: #fff;
  background-color: #409EFF;
  border-radius: 4px;
  overflow: hidden;
  display: inline-block;
  *display: inline;
  *zoom: 1;
  float:left;
}
.a-upload  input {
  position: absolute;
  font-size: 100px;
  right: 0;
  top: 0;
  opacity: 0;
  filter: alpha(opacity=0);
  cursor: pointer
}

.upload-btn:hover ,
.a-upload:hover {
  color: #fff;
  background-color: #66B1FF;
  text-decoration: none
}
  .uploadParent{
    width:400px;
    height:300px;
    border: 2px solid #ccc;
    background-color: #f8f8f8;
    position:fixed;
    top:200px;
    left:40%;
    z-index:100000;
  }
  .uploadParent button{
    float:left;
  }
  .uploadBtnParent{
    height:100px;
  }
  .uploadParent{
    box-sizing: border-box;
    padding:6px;
  }
  .uploadParent table{
    border-collapse: collapse;
    width:380px;
  }
  .uploadParent table tr td{
    height:30px;
  }
  .uploadParent table tr td:first-child{
    width:220px;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
  }
  .uploadParent table tr td:nth-child(2){
    width:80px;
  }
  .uploadParent table tr td:last-child{
    width:80px;
  }
  .tableParent{
    width:380px;
    height:160px;
    overflow: auto;
  }
  .tableParent p{
    padding-left:10px;
  }
  .closeTip{
    width:20px;
    height:20px;
    border-radius: 50%;
    text-align: center;
    line-height: 20px;
    position: absolute;
    right:-10px;
    top:-10px;
    background-color: #0059B3;
    color:#fff;
    cursor:pointer;
  }
  button{
    outline:none;
  }
</style>



