<template>
  <div id="tmpl">
    <!-------------------查询条件----------------------------->
    <div class="searchs">
      <span>查询区间</span>
      <el-date-picker
        class="searchInputs"
        size="small"
        v-model="startTime"
        type="date"
        placeholder="选择日期">
      </el-date-picker>
      <span>至</span>
      <el-date-picker
        class="searchInputs"
        size="small"
        v-model="endTime"
        type="date"
        placeholder="选择日期">
      </el-date-picker>
      <el-input
        class="searchInputs"
        size="small"
        placeholder="请输入要查询医生"
        v-model="searchDoctor"
        clearable>
      </el-input>
      <el-input
        class="searchInputs"
        size="small"
        placeholder="请输入要查询手术名称"
        v-model="searchOpe"
        clearable>
      </el-input>
      <el-input
        class="searchInputs"
        size="small"
        placeholder="请输入要查询患者"
        v-model="searchMan"
        clearable>
      </el-input>
      <el-button size="small" type="primary" @click="getTableData">查询</el-button>
    </div>

    <!-----------------------------表格展示----------------------->
    <div class="contentTable">
      <el-table
        :data="tableData3"
        :max-height="tableHeight"
        border
        :header-cell-style="headStyle"
        style="width: 100%">
        <el-table-column
          prop="chiefDoctor"
          label="医生"
          min-width="10%">
        </el-table-column>
        <el-table-column
          prop="operationName"
          label="手术名称"
          min-width="20%">
        </el-table-column>
        <el-table-column
          prop="patientName"
          label="患者"
          min-width="10%">
        </el-table-column>
        <el-table-column
          prop="operationRoom"
          label="手术室"
          min-width="10%">
        </el-table-column>
        <el-table-column
          prop="operationDate"
          label="日期"
          min-width="10%">
        </el-table-column>
        <el-table-column
          prop="operationTime"
          label="时间"
          min-width="10%">
        </el-table-column>
      </el-table>
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="1"
        :page-sizes="[5, 10, 15, 20]"
        :page-size="5"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </div>
  </div>
</template>

<script>
  export default {
    data:function(){
      return {
        tableHeight:0,//初始化表格高度，自适应屏幕
        /*-------------搜索栏各项参数-------------------*/
        startTime: '2018-7-24',//开始日期
        endTime:'2018-7-24',//结束日期
        searchDoctor:'',//查询的医生名称
        searchOpe:'',//查询的手术名称
        searchMan:'',//查询的患者名称
        /*---------------分页各项参数-------------------*/
        pageNumbers:'',//当前的页数
        pageSize:'5',//每页显示的条数
        total:0,//接受到后台传递查询结果的总条数
        //表格模拟数据
        tableData3: []
      }
    },
    created:function(){
      this.getTableHeight();
      this.getTableData();
    },
    methods:{
      //设置表头的样式
      headStyle:function(row, column, rowIndex, columnIndex){
        return 'tableThStyle'
      },
      //初始化表格最大高度，根据屏幕自适应
      getTableHeight:function(){
        var innerHeights = window.innerHeight-220;
        this.tableHeight = innerHeights;
        var nowTime = new Date();
        var year = nowTime.getFullYear();
        var month = nowTime.getMonth()+1;
        var day = nowTime.getDate();
        if (month >= 1 && month <= 9) {
          month = "0" + month;
        }
        if (day >= 0 && day <= 9) {
          day = "0" + day;
        }
        //console.log(year,month,day);
        //this.startTime = year+'-'+month+'-'+day;
        //this.endTime = year+'-'+month+'-'+day;
        //console.log(this.startTime);
      },
      getTableData:function(){
        var url = '/api/com/operationManager/getOperationManagerList';
        this.$http.get(url,{params:{
          sData:this.startTime,
          eData:this.endTime,
          pname:this.searchMan,
          opeName:this.searchOpe,
          doctor:this.searchDoctor,
          page:this.pageNumbers,
          size:this.pageSize}}).then(function(res){
            //console.log(res);
          if(res.body.zt == 'true'){
            this.tableData3 = res.body.data;
            this.total = res.body.total;
          }
        });
      },
      handleSizeChange:function(val) {
        //console.log(`每页 ${val} 条`);
        this.pageSize = val;
        this.getTableData();
      },
      handleCurrentChange:function(val) {
        //console.log(`当前页: ${val}`);
        this.pageNumbers = val;
        this.getTableData();
      }
    }
  }
</script>

<style scoped>
  #tmpl{
    margin-top: 60px;
  }
.searchs{
  box-sizing: border-box;
  padding:10px 20px;
  text-align: left;
}
.searchInputs{
  width:150px;
}
.tableThStyle{
  text-align: center;
}
</style>



