import Vue from 'vue'
import Router from 'vue-router'
import store from '../store/store'

import Login from '@/components/routers/login'
import S404 from '@/components/routers/404'
import Mains from '@/components/routers/mains'

import DayAccessSurgery from '@/components/childrenRouter/DayAccessSurgery'
import PatientAssessment from '@/components/childrenRouter/PatientAssessment'
import doctorChoice from '@/components/childrenRouter/doctorChoice'
import AppointmentScheduleDoctor from '@/components/childrenRouter/AppointmentScheduleDoctor/AppointmentScheduleDoctor'
import menuSetting from '@/components/childrenRouter/systormSetting/menuSetting'
import roleSetting from '@/components/childrenRouter/systormSetting/roleSetting'
import userSetting from '@/components/childrenRouter/systormSetting/userSetting'
import opeMessagePage from '@/components/childrenRouter/systormSetting/opeMessagePage'

Vue.use(Router)

let router = new Router({
  routes: [
    {path:'/',name:'toLogin',redirect:'/login'},
    {path:'/login',name:'login',component:Login},
    {path:'/s404',name:'s404',component:S404},
    {path:'/mains',name:'mains',component:Mains,
      children:[
        //日间手术准入
        {
          path: '/DayAccessSurgery',
          name: 'DayAccessSurgery',
          component: DayAccessSurgery
        }
        //患者准入评估
        ,{
          path: '/PatientAssessment',
          name: 'PatientAssessment',
          component: PatientAssessment
        }
        ,{
          path:'/doctorChoice',
          name:'doctorChoice',
          component:doctorChoice
        }
        //预约排程医生
        ,{
          path:'/AppointmentScheduleDoctor/AppointmentScheduleDoctor',
          name:'AppointmentScheduleDoctor',
          component:AppointmentScheduleDoctor
        }
        //系统设置
        ,{
          path:'/systormSetting/menuSetting',
          name:'菜单管理',
          component:menuSetting
        }
        //角色管理
        ,{
          path:'/systormSetting/roleSetting',
          name:'角色管理',
          component:roleSetting
        }
        //用户管理
        ,{
          path:'/systormSetting/userSetting',
          name:'用户管理',
          component:userSetting
        }
        //患者留言
        ,{
          path:'/systormSetting/opeMessagePage',
          name:'患者留言',
          component:opeMessagePage
        }
      ]
    }
  ]
});
router.beforeEach(function(to,from,next){
  if(to.matched.length === 0){
    from.name?next({name:from.name}):next('/');
  }else{
    next();
  }
});
export default router;
