<!--菜单管理-->
<template>
  <div id="tmpl">
    <div id="searchGroup">
      <el-input size="small" v-model="menuName" class="searchInput" placeholder="请输入要查询的菜单名称" clearable></el-input>
      <el-button type="primary" size="small">查询</el-button>
      <el-button type="primary" size="small" @click="formShowHide">添加</el-button>
      <el-dialog title="增加" :visible.sync="dialogFormVisible">
        <el-form :model="form">
          <el-form-item label="菜单名称" :label-width="formLabelWidth">
            <el-input v-model="form.name" auto-complete="off" clearable></el-input>
          </el-form-item>
          <el-form-item label="菜单等级" :label-width="formLabelWidth">
            <el-select v-model="form.level" placeholder="请选择菜单等级" @change="getParentMenu">
              <el-option label="一级菜单" value="0"></el-option>
              <el-option label="二级菜单" value="1"></el-option>
              <el-option label="三级菜单" value="2"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="父级菜单" :label-width="formLabelWidth">
            <el-select @change="selectOne" v-model="form.pName" placeholder="请选择父级菜单等级">
              <el-option v-if="form.level == 0" label="无" :value="0"></el-option>
              <el-option v-else v-for="(item,index) in pNames" :key="item.id" :label="item.name" :value="item.id"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="请求地址" :label-width="formLabelWidth">
            <el-input v-model="form.url" auto-complete="off" clearable></el-input>
          </el-form-item>
          <el-form-item label="排序值" :label-width="formLabelWidth">
            <el-input v-model="form.paixu" auto-complete="off" clearable></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="closeDialog">取 消</el-button>
          <el-button type="primary" @click="addNewData">确 定</el-button>
        </div>
      </el-dialog>
      <el-button type="primary" size="small" @click="delRow">删除</el-button>
    </div>
    <el-table
      ref="multipleTable"
      :data="tableData"
      tooltip-effect="dark"
      :highlight-current-row="true"
      style="width: 100%"
      @selection-change="handleSelectionChange"
      @cell-click="clickRow">
      <el-table-column
        type="selection"
        width="55">
      </el-table-column>
      <el-table-column
        prop="name"
        label="菜单名称">
        <template slot-scope="scope">
          <span style="color:#09f;cursor: pointer">{{ scope.row.name }}</span>
        </template>
      </el-table-column>
      <el-table-column
        prop="level"
        label="菜单等级">
      </el-table-column>
      <el-table-column
        prop="pName"
        label="父级菜单"
        show-overflow-tooltip>
      </el-table-column>
      <el-table-column
        prop="url"
        label="请求地址"
        show-overflow-tooltip>
      </el-table-column>
      <el-table-column
        prop="level"
        label="排序值"
        show-overflow-tooltip>
      </el-table-column>
    </el-table>
    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="pageNum"
      :page-sizes="[5, 10, 15, 20]"
      :page-size="pageSize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="total">
    </el-pagination>
  </div>
</template>

<script>
  export default {
    data:function(){
        return {
          delID:'',//删除表格中行的id集合
          //父级菜单选中项的ID值
          pOptionsId:0,
          //标志清空下拉菜单的数据
          clearSelectData:false,
          //选择菜单等级后，查询到的父级菜单数据
          pNames:[],
          //新增菜单的弹出框
          dialogFormVisible:true,//控制增加、修改框的显示隐藏
          form: {
            pName:'',
            name: '',
            level: '',
            pLevel: '',
            url: '',
            paixu: '',
            delivery: false,
            type: [],
            resource: '',
            desc: '',
            id:''//菜单ID值
          },//表单值
          formLabelWidth: '120px',//表单大小
          //查询
          menuName:'',//输入框中的手术名称
          //分页
          total:0,//查询结果的总条数
          pageSize:5,//默认每页的条数
          pageNum:1,//当前的页数
          //表格
          tableData:[
            {
            "pName":"无",
            "level":0,
            "name":"11",
            "pid":0,
            "id":3,
            "descript":"11",
            "url":"11"
            }
          ],
          multipleSelection: []
        }
    },
    created:function(){
      this.getTableData();
      this.formShowHide();
    },
    mounted:function(){
      this.clearAllForm();
    },
    methods:{
      //根据选中行的ID值，删除当前行，然后刷新页面
      delRow:function(){
        var x = confirm('确定删除选中行？');
        if(x == true){
          //console.log(this.delID);
          var url = '/api/com/permission/deletePermissionById';
          this.$http.post(url,{ids:this.delID},{emulateJSON:true}).then(function(res){
            //console.log(res);
            if(res.body.zt == 'true'){
              this.getTableData();
            }
          });
        }else{
          //console.log(2222);
        }
      },
      //关闭对话框
      closeDialog:function(){
        this.form.name = '';//菜单名
        this.form.level = '';//菜单等级
        this.form.url = '';//菜单地址
        this.form.pName = '';//父级菜单
        this.form.paixu = '';//菜单排序值
        //console.log(this.form);
        //console.log('qqq');
        this.dialogFormVisible = false;
      },
      //只要关闭对话框的数据为反的，就重置
      clearAllForm:function(){
        if(this.dialogFormVisible = false){
          this.form.name = '';//菜单名
          this.form.level = '';//菜单等级
          this.form.pName = '';//父级菜单
          this.form.url = '';//菜单地址
          this.form.paixu = '';//菜单排序值
        }
      },
      //新增一条数据
      addNewData:function(){
        //console.log(this.form.level+' :pName');
        if(this.form.name == ''){
          this.$message({
            showClose: true,
            message: '菜单名称不能为空',
            type: 'warning'
          });
          return
        }else if(this.form.level == ''){
          this.$message({
            showClose: true,
            message: '菜单等级不能为空',
            type: 'warning'
          });
          return
        }else if(this.form.pName == null){
          this.$message({
            showClose: true,
            message: '父级菜单不能为空',
            type: 'warning'
          });
          return
        }else{
          var url = '/api/com/permission/insertPermission';
          if(this.form.level == '一级菜单'){
            this.form.level = 0;
          }
          if(this.form.level == '二级菜单'){
            this.form.level = 1;
          }
          if(this.form.level == '三级菜单'){
            this.form.level = 2;
          }
         // if(this.form.id == ''){
            this.form.id = 0;
          //}
          //console.log(this.form.url+'菜单地址， ',this.form.name+'菜单名, ',this.form.level+'菜单等级, ',this.pOptionsId+'父级菜单ID, ',this.form.id+'菜单ID');
          this.$http.post(url,{
            id:this.form.id,//菜单id
            name:this.form.name,//菜单名
            level:this.form.level,//菜单等级
            pid:this.pOptionsId,//父级菜单ID
            url:this.form.url//菜单地址
          },{emulateJSON:true}).then(function(res){
            //console.log(this.form.name,this.form.level,this.pOptionsId);
            if(res.body.zt == 'true'){
              //console.log(this.form.level);
              this.getTableData();
              this.$message({
                showClose: true,
                message: '添加菜单成功',
                type: 'success'
              });
              this.form.name = '';//菜单名
              this.form.level = '';//菜单等级
              this.form.pName = '';//父级菜单
              this.form.url = '';//菜单地址
              this.form.paixu = '';//菜单排序值
            }else{
              this.$message({
                showClose: true,
                message: '添加菜单失败',
                type: 'success'
              });
            }
          });
          this.dialogFormVisible = false;
        }

      },
      //清空下拉菜单选择项
      clearSelect:function(){
        //console.log(1111);
        this.clearSelectData = true;
      },
      //获取父级下拉菜单的选中项
      selectOne:function(value){
        //console.log(value);
        this.pOptionsId = value;
      },
      //选择菜单等级，查询父级菜单
      getParentMenu:function(){
        //this.form.pName = '';
        var url = '/api/com/permission/getPermissionListByLevel';
        var levels = this.form.level;
        //console.log(levels+' levels');
          if(levels == '一级菜单'){
            levels = 0;
          }
          if(levels == '二级菜单'){
            levels = 1;
          }
          if(levels == '三级菜单'){
            levels = 2;
          }
        this.$http.get(url,{params:{
          level:levels
        }}).then(function(res){
          //console.log(res);
          //console.log(levels);
          if(res.body.zt == 'true'){
              this.pNames = res.body.data;
          }
        });
      },
      //控制新增弹框的显示隐藏
      formShowHide:function(){
        this.dialogFormVisible = !this.dialogFormVisible;
      },
      //表格
      toggleSelection:function(rows) {
        if (rows) {
          rows.forEach(function(row){
            this.$refs.multipleTable.toggleRowSelection(row);
          });
        } else {
          this.$refs.multipleTable.clearSelection();
        }
      },
      //获得选中行的菜单id集合
      handleSelectionChange:function(val) {
        this.multipleSelection = val;
        //console.log(val[0].id);
        var ids = '';
        for(var i=0;i<val.length;i++){
          ids += val[i].id+',';
        }
        //console.log(ids);
        this.delID = ids;
      },
      //分页
      handleSizeChange:function(val) {
        //console.log(`每页 ${val} 条`);
        this.pageSize = val;
        this.getTableData();
      },
      handleCurrentChange:function(val) {
        //console.log(`当前页: ${val}`);
        this.pageNum = val;
        this.getTableData();
      },
      //手写获得菜单表格数据
      getTableData:function(){
        var url = '/api/com/permission/getPermissionList';
        //console.log(this.menuName,this.pageNum,this.pageSize);
        this.$http.post(url,{
          name:this.menuName,
          page:this.pageNum,
          size:this.pageSize
        },{emulateJSON:true}).then(function(res){
          if(res.body.zt === 'true'){
            this.total = res.body.total;
            this.tableData = res.body.data;

            for(var i=0;i<this.tableData.length;i++){
              //console.log(this.tableData[i].level)
              if(this.tableData[i].level == 0){
                this.tableData[i].level = '一级菜单'
              }else if(this.tableData[i].level == 1){
                this.tableData[i].level = '二级菜单'
              }else if(this.tableData[i].level == 2){
                this.tableData[i].level = '三级菜单'
              }
            }
            //console.log(this.tableData.level);
          }

        });
      },
      //点击行修改菜单
      clickRow:function(row, column, cell, event) {
        this.form.id = row.id;
        //console.log(column);
        if(column.label == '菜单名称'){
        var url = '/api/com/permission/getPermissionById';
        this.$http.post(url, {id: row.id}, {emulateJSON: true}).then(function (res) {
          //console.log(row);
          //console.log(res);
          if (res.body.zt == 'true') {
            if (res.body.data.level == 0) {
              res.body.data.level = '一级菜单';
            } else if (res.body.data.level == 1) {
              res.body.data.level = '二级菜单';
            } else {
              res.body.data.level = '三级菜单';
            }
            //console.log(res);
            this.form = res.body.data;
            this.pOptionsId = res.body.data.pid;

            //调用查询父级菜单的方法
            this.getParentMenu();
            //this.getTableData();
            this.dialogFormVisible = true;
          }
        });
      }
      }
    }
  }
</script>

<style scoped>
#tmpl{
  margin-top: 60px;
}
#searchGroup{
  text-align: left;
  height:62px;
  line-height: 50px;
}
  .searchInput{
    width:150px;
  }
.el-select,
.el-form .el-input{
  width:50%;
}
</style>



